<template>
  <div class="home">
    <b-navbar toggleable="md" type="dark" variant="dark">
      <b-navbar-brand>Warehouse Booking</b-navbar-brand>
      <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>
      <b-collapse id="nav-collapse" is-nav>
        <b-navbar-nav>
          <div 
            v-for="(nav, i) in navs"
            :key="i">
            <b-nav-item
              :active="$route.path.indexOf(nav.url) >= 0"
              @click="$router.push(nav.url)"
              v-if="nav.userType.indexOf(user.userType) >= 0">
              <span class="px-3"><b-icon :icon="nav.icon" class="mr-2"></b-icon>{{ nav.label }}</span>
            </b-nav-item>
          </div>
        </b-navbar-nav>
        <b-navbar-nav class="ml-auto">
          <b-nav-item-dropdown right class="px-3">
            <template #button-content>
              <b-icon icon="person"></b-icon> {{ user.username }}
            </template>
            <b-dropdown-item @click="$router.push('/home/password')"><b-icon icon="gear" class="mr-2"></b-icon> Change Password</b-dropdown-item>
            <b-dropdown-item @click="handleLogout"><b-icon icon="power" class="mr-2"></b-icon>Sign Out</b-dropdown-item>
          </b-nav-item-dropdown>
        </b-navbar-nav>
      </b-collapse>
    </b-navbar>
    <b-container class="mt-4">
      <router-view/>
    </b-container>
  </div>
</template>
<script>
  import { mapState } from 'vuex'
  export default {
    name: 'HomeView',
    data() {
      return {
        navs: [
          { url: '/home/calendar', label: 'Calendar', icon: 'calendar', userType: ['user', 'admin', 'superadmin'] },
          { url: '/home/country', label: 'Countries', icon: 'geo-alt', userType: ['superadmin']},
          { url: '/home/company', label: 'Companies', icon: 'truck', userType: ['superadmin'] },
          { url: '/home/user', label: 'Users', icon: 'person', userType: ['superadmin']},
          { url: '/home/warehouse', label: 'Warehouses', icon: 'box', userType: ['superadmin']}
        ]
      }
    },
    computed: {
      ...mapState({
        user: state => state.auth.user
      }) 
    },
    methods: {
      handleLogout() {
        this.$store.dispatch('auth/logout').then(() => {
          this.$router.push('/')
        })
      }
    }
  }
</script>
